const xss = require("xss");
/**
 * @description:xss
 * @param {string} Html 
 * @param {array} format html
 * @param {array} attr 
 * @return {string} htmlString
 */
function formatXss(Html, format = [], attr = []){
    //
    const tag = [
        "p",
        "a",
        "img",
        "font",
        "span",
        "b",
        "blockquote",
        "code",
        "h1",
        "h2",
        "h3",
        "h4",
        "h5",
        "h6",
        "hr",
        "br",
        "s",
        "i",
        "u",
        "strike",
        "div",
        "strong",
        "pre"
    ];
    if (format.length > 0) {
        format.forEach(el => {
            tag.push(el);
        })
    }
    //
    const can = ["color", "size", "style", "href", "src"];
    if (attr.length > 0) {
        attr.map(e => {
            can.push(e)
        })
    }
    let tmp = {};
    for (let index = 0; index < tag.length; index++) {
        const element = tag[index];
        tmp[element] = can;
    }
    let text = xss(Html, {
        whiteList: tmp,
    });
    return text;
}

module.exports = formatXss
